.form-validate-result {
    background-color: #fff;
    padding-top: pxToVw3(20);
    padding-bottom: pxToVw3(20);
    padding-left: pxToVw3(25);

    color: #005090;
    display: flex;
    align-items: center;

    margin-top: pxToVw3(30);

    transition: background-color 0.2s ease-in-out;
    .icon-wrapper {
        margin-right: 40px;
    }

    p {
        margin: 0;
        font-weight: 500;
    }

    &--blue {
        color: #fff;
        background-color: #005090;
    }

    &.error {
        background-color:#bd362f;
    }
}

@media (max-width: 767px) {
    .form-validate-result {
        font-size: 14px;
        padding-top: 5px;
        padding-bottom: 5px;
    }
}


